@each $name, $color in $color-years {
  .t-#{$name} {
    .c-preface {
      background-color: darken($color, 25%);
    }

    .c-banner {
      background-color: darken($color, 30%);
    }

    .c-traverse-nav__item::after {
      background-color: rgba(darken($color, 30%), 0.75);
    }

    .s-prose--article {
      h2 {
        color: darken($color, 20%);
      }

      .lede {
        color: darken($color, 30%);
      }
    }
  }
}
